import { host } from './../../../environments/environment.prod';

import { Album } from './../../interface/menu';
import { Component, OnInit,Input } from '@angular/core';
import { Imagenes } from 'src/app/interface/imagenes';
import { ImagenesService } from 'src/app/serviciosCall/imagenes.service';

@Component({
  selector: 'app-album',
  templateUrl: './album.component.html',
  styleUrls: ['./album.component.css'],
  
})


export class AlbumComponent implements OnInit {
  @Input('path') path:string;  
  @Input('titulo')titulo:string;  
  @Input('idScroll')idScroll:string;      
  @Input()widhtImg=110;  
  @Input()heigthImg=100;
  @Input()margin=40;

  public Titulo = ""
  public album = new Array<Album>();
  private clockScroll:any;  
  constructor(
    private servicesImagenes :ImagenesService
  ) { 
    
    
    
  }

  ngOnInit() {
    this.Titulo = this.titulo;

    this.LlenarAlbum();    
    
  }

  


  LlenarAlbum()
  {
    const url = this.path;
    let imagenesServidor = new Array<Imagenes>()
    this.servicesImagenes.get(url).subscribe((retorno:any)=>{
      imagenesServidor = retorno;
      for(let recorre=0; recorre < imagenesServidor.length ; recorre++)
      {
        const recorreObjeto = imagenesServidor[recorre];
        this.album.push({
          id: recorreObjeto.id,
          path: host+recorreObjeto.path,
          widthImg:this.widhtImg,
          heigthImg: this.heigthImg
        })
      }

      if(imagenesServidor.length === 0)  
      {
        this.album.push({
          id :1,
          path: "assets/img/sorry.png",
          widthImg:this.widhtImg,
          heigthImg: this.heigthImg
        })
      }
    },error=>{
      console.log(error);
    })    
  }

  

  FilterAlbums(desde:number, hasta:number)
  {
    return this.album.filter(x => x.id >= desde && x.id <=hasta )
  }

  Next(tipo:string)
  {
    const card = document.getElementById("album");
    let scroll = document.getElementById(this.idScroll);    
    let viewPortScrolll = Number(scroll.getBoundingClientRect().width.toFixed(0));    
    const item = Number((<HTMLElement>document.querySelector( `#${this.idScroll} div` )).getBoundingClientRect().width.toFixed(0));    
    

    let move = 0;        
    const twoitems = item*2;
    if(viewPortScrolll > Number(twoitems.toFixed(0)))
    {
      viewPortScrolll = Number((viewPortScrolll / 2).toFixed(0));
    }
    
    
    if(tipo.toUpperCase() === "R")
    {
      move = scroll.scrollLeft + viewPortScrolll;
            
    }else{
      
        move = scroll.scrollLeft - viewPortScrolll;      
    }
    
    if(Number(scroll.scrollLeft.toFixed(0)) >= (Number(scroll.scrollWidth.toFixed(0))-Number(card.getBoundingClientRect().width.toFixed(0))))
    { 
      if(tipo.toUpperCase() === "R")     
      {
        move = 0      
      }
      
    }else{
      if(scroll.scrollLeft === 0) 
      {
        if(tipo.toUpperCase() === "L")     
        {
          move = scroll.scrollWidth
        } 
        
      }
    }    
    scroll.scrollTo({
      top:0,
      left:move,
      behavior: 'smooth'
    })

    
  }
}

 



